  handleClick = (e) => {
    const { innerText } = e.target;
    const { lastPressed, currentNumber, pervNumber } = this.state;

    if (!Number.isNaN(Number(innerText))) {
      if (currentNumber === "0") {
        this.setState({
          currentNumber: innerText,
        });
      } else {
        this.setState({
          currentNumber: currentNumber + innerText,
        });
      }
    }
    switch (innerText) {
      case 'AC':{
        this.setState({
          currentNumber: '0',
          pervNumber: undefined
        });
        break;
      }
    }
    this.setState({
      lastPressed: innerText,
    });

    // alert(innerText);
  };
